:root {
  --font-text: "Roboto", sans-serif;
  --font-size-small: 12px;
  --bg-color-white: #fff;
  --text-color-black: #000;
  --text-color-blue: #00acdc;
}

body {
  font-family: var(--font-text);
  font-size: var(--font-size-small);
  color: var(--text-color-black);
  margin: 0;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  object-fit: contain;
  width: 40px;
  height: 40px;
}

.logo__img {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  margin: 0 0 0 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: var(--text-color-black);
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.is-active, .nav__link:hover, .nav__link.is-active {
  color: var(--text-color-blue);
}

.is-active:link {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--text-color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

